import "./ChallengeModal.css";
import { useState } from "react";

function ChallengeModal(props) {
  const [style, setStyle] = useState({});
  const [flagControl, setFlagControl] = useState('')
  const [flagAnswer, setFlagAnswer] = useState('')
  const modalClass = props.modalActive
    ? "challengeModalMain active"
    : "challengeModalMain";
  const modalBody = props.modalActive ? "modalCard active" : "modalCard";
  const closeModal = () => {
    props.sendDataToParent(false);
    setTimeout(() => {
      setStyle({ animation: " fadeOut .5s linear" });

    }, 500);
    setTimeout(() => {

      setStyle({});
    }, 500);
    setFlagAnswer('')
  };
  const flagSubmit = (e) => {
    e.preventDefault();
    const token = sessionStorage.getItem('token');
    const userData = {
      userID: Number(sessionStorage.getItem('userId')),
      dbID: sessionStorage.getItem('dbId'),
      challengeIndex: Number(props.index),
      flag:flagControl
    };
    fetch("http://127.0.0.1:3001/flag-control", {
      method: "POST",
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(userData),
    })
      .then((response) => response.json())
      .then((data) => {
        if(data.message==='Doğru Cevap'){
          window.location.reload();
        }else{
          setFlagAnswer('Yanlış Cevap!')
        }
      });
  }
  const handleChange = (e) => {
    console.log(props.index)
    setFlagControl(e.target.value)
  }
  return (
    <div>
      <div onClick={closeModal} className={modalClass} style={style}></div>
      <div style={style} className={modalBody}><section className="modal-header"><p className="modalChallengeName">{props.challengeName}</p><p className="modalChallengePoints">{props.points}</p></section><section className="modal-body"><p className="modalDesc">{props.description}</p></section><section className="modal-flag"><form onSubmit={flagSubmit} className="button-group"><input onChange={handleChange} className="flagInput" type="text" placeholder="TIMTAL={...}"></input><button className="flagSubmit">Kontrol</button></form><p className="wrongAnswer">{flagAnswer}</p></section></div>
    </div>
  );
}

export default ChallengeModal;
